r\ninput,\r\nselect {\r\n  display: block;\r\n  font-family: inherit;\r\n  font-size: 100%;\r\n  margin: 0;\r\n  box-sizing: border-box;\r\n  min-height: 30px;\r\n  border-radius: 3px;\r\n}\r\n\r\ninput,\r\ntextarea,\r\nselect {\r\n  border: 1px solid grey;\r\n}\r\n\r\nbutton {\r\n  background-color: var(--button-clr);\r\n  cursor: pointer;\r\n}\r\n\r\nbutton:hover {\r\n  background-color: var(--hover-clr);\r\n}\r\nbutton:active {\r\n  background-color: var(--active-clr);\r\n}\r\n\r\n#page {\r\n  display: grid;\r\n  grid-template-columns: auto 1fr;\r\n  grid-template-rows: min-content 1fr;\r\n  min-height: 100vh;\r\n  min-height: 100dvh;\r\n\r\n  > * {\r\n    padding: 1em;\r\n    margin: 0;\r\n  }\r\n\r\n  .buttonStyle {\r\n    background-color: var(--button-clr);\r\n    padding: 0.5em;\r\n  }\r\n  .buttonStyle:hover {\r\n    background-color: var(--hover-clr);\r\n  }\r\n  .buttonStyle:active {\r\n    background-color: var(--active-clr);\r\n  }\r\n}\r\n\r\n#sideBar {\r\n  grid-area: 1 / 1 / 3 / 2;\r\n  background-color: var(--sideBar-clr);\r\n  width: 20ch;\r\n  padding: 1em;\r\n  border-right: 1px solid var(--line-clr);\r\n  max-height: 100vh;\r\n  transition: 1s ease-in-out;\r\n  text-wrap: nowrap;\r\n  overflow: hidden;\r\n\r\n  svg {\r\n    flex-shrink: 0;\r\n  }\r\n\r\n  button {\r\n    border: none;\r\n    background: none;\r\n    background-color: none;\r\n    text-align: left;\r\n  }\r\n\r\n  button:hover {\r\n    background-color: var(--button-clr);\r\n  }\r\n  button:active {\r\n    background-color: var(--active-clr);\r\n  }\r\n\r\n  ul > li {\r\n    svg {\r\n      height: 2em;\r\n    }\r\n\r\n    display: flex;\r\n    align-items: center;\r\n    span {\r\n      padding-left: 0.5em;\r\n      flex-grow: 1;\r\n    }\r\n  }\r\n\r\n  #sideBarTop {\r\n    margin-left: auto;\r\n    max-width: 100%;\r\n    gap: 1em;\r\n    justify-content: flex-end;\r\n    padding: 0;\r\n    margin-bottom: 0.5em;\r\n  }\r\n\r\n  > ul {\r\n    list-style: none;\r\n    display: flex;\r\n    flex-direction: column;\r\n\r\n    gap: 1.5em;\r\n    max-height: 100%;\r\n  }\r\n\r\n  #searchLi{\r\n    \r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: start;\r\n    gap: 0.5em;\r\n    input{\r\n      width: 100%;\r\n      font-size: smaller;\r\n    }\r\n    >div{\r\n      display: flex;\r\n    }\r\n\r\n    .hidden{\r\n      display: none;\r\n    }\r\n  }\r\n}\r\n/* for all the rotatable svg, controlled through js by adding the rotate class to the parent */\r\n.rotateEnable {\r\n  transition: rotate 1s ease;\r\n}\r\n\r\n.rotate .rotateEnable {\r\n  rotate: 180deg;\r\n}\r\n\r\n#sideBar li:first-child ~ * {\r\n  opacity: 1;\r\n  transition-property: display opacity;\r\n  transition-duration: 1s;\r\n  transition-behavior: allow-discrete;\r\n}\r\n\r\n@starting-style {\r\n  #sideBar li:first-child ~ * {\r\n    opacity: 0;\r\n  }\r\n}\r\n\r\ndialog {\r\n  display: none;\r\n  opacity: 0;\r\n  transition-property: display opacity;\r\n  transition-duration: 1s;\r\n  transition-behavior: allow-discrete;\r\n}\r\n\r\ndialog[open] {\r\n  display: block;\r\n  opacity: 1;\r\n  translate: 0 0;\r\n  margin-top: 6rem;\r\n  /* the transition from close position/ display none because no start value exist when you transition from none */\r\n  @starting-style {\r\n    opacity: 0;\r\n    translate: 0 -25vh;\r\n  }\r\n}\r\n\r\ndialog::backdrop {\r\n  opacity: 0;\r\n  transition-property: overlay display opacity;\r\n  transition-duration: 1s;\r\n  transition-behavior: allow-discrete;\r\n  background-image: linear-gradient(#e66465, #9198e5);\r\n}\r\n\r\ndialog[open]::backdrop {\r\n  opacity: 0.75;\r\n}\r\n\r\n@starting-style {\r\n  dialog[open]::backdrop {\r\n    opacity: 0;\r\n  }\r\n}\r\n\r\n#sideBar.close {\r\n  padding: 0.5rem;\r\n  width: 3rem;\r\n\r\n  li:first-child ~ * {\r\n    overflow: hidden;\r\n    display: none;\r\n    opacity: 0;\r\n  }\r\n}\r\n\r\n#sideBar .dropMenu {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: flex